.carrinho{

    &__item{

        &__servico{
            max-width: 250px;
            
            &__title{
                margin-bottom: 10px;
                font-size: 17px;
                color: rgb(139, 139, 139);
            }

            &__terapeuta{
                margin-bottom: 15px;
                font-size: 15px;
                font-weight: lighter;
                color: rgb(158, 158, 158);
            }
        }

        &__data{
            font-size: 12px;
            min-width: 180px;

            a{
                text-decoration: underline;
            }
        }
    }

    &__agendar{
        padding: 15px 0;
        display: flex;
        justify-content: center;
        align-items: center; 
        border-bottom: 0.5px solid #F0F0F7;

        &__btn{
            width: 70px;
            height: 70px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 19px;
            background-color: rgb(233, 238, 241);
            color: $mh-color-primary;
            border: none;
            @include transition();

            &:hover{
                @include scale(1.05);
            }
        }
    }

    &__total{
        display: flex;
        justify-content: flex-end;    
        padding: 40px 100px 20px 0;
    }

    &__buttons{
        display: flex;
        justify-content: flex-end; 
        padding: 20px 0;

        @media screen and(max-width: $bp-SM){
            flex-direction: column;

            .order-last{
                margin-top: 10px;
            }
        }
       
    }
    
}