.nossos-artigos {
    background-color: #F0F0F7;
    min-height: 400px;

    &__caption {
        &__title {
            @include title();
            color: $mh-color-blue;

            @media screen and(max-width: $bp-MD) {
                font-size: 30px;
            }
        }

        @media screen and(max-width: $bp-MD) {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
        }
    }

    .owl-nav {
        display: flex;
        justify-content: flex-end;

        @media screen and(max-width: $bp-LG) {
            justify-content: center !important;
            margin-bottom: 10px;
        }

        button {
            cursor: pointer;
            background: none;
            outline: none;
            border: none;
            font-size: 30px;
            color: $mh-color-blue;
            &:not(:first-child) {
                margin-left: 6px;
            }
        }
        .owl-nav {
            display: flex;
            justify-content: flex-end;
            @media screen and(max-width: $bp-LG) {
                justify-content: center !important;
                margin-bottom: 10px;
            }

            button {
                cursor: pointer;
                background: none;
                outline: none;
                border: none;
                font-size: 30px;
                color: $mh-color-blue;
                &:not(:first-child) {
                    margin-left: 6px;
                }
            }
        }
    }
}
