.section-wraper {
    padding: 50px 0;
}

.inner-title {
    @include title();
    color: #839097;

    @media screen and(max-width: $bp-MD){
        font-size: 30px;
        line-height: 32px;
    }
}

.inner-desc {
    letter-spacing: 0.2px;
    text-align: center;
    color: rgb(173, 173, 173);
    font-size: 18px;
}

.color-primary {
    color: $mh-color-primary !important;
}

.color-blue {
    color: $mh-color-blue !important;
}

.color-secondary {
    color: $mh-color-secondary !important;
}

.color-gray {
    color: $mh-color-gray !important;
}

.border-light {
    border: .5px solid rgb(228, 228, 228) !important;
}

.price{
    font-size: 30px;
    font-weight: bold;
    color: $mh-color-primary !important;

    .total{
        font-size: 25px;
        margin-right: 30px;
        font-weight: 500;
    }
    
    .sign{
        font-size: 15px;
        font-weight: 500;
    }
}

.img-70{
    width: 70px;
    height: 70px;
    object-fit: cover;
}

.circle{
    border-radius: 50%;
}
