.produto-info{
    &__img{
        width: 100%;
        height: 300px;
        
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__desc{
        &__title{
            @include title();
            color: $mh-color-primary;
        }

        &__terapeuta{
            font-size: 23px;
            font-weight: lighter;
            padding: 10px 0;
            color: $mh-color-gray;
            letter-spacing: 1px;
        }

        &__info{
            font-weight: lighter;
            color: rgb(163, 163, 163);
            letter-spacing: 1px;
        }
    }
}