.info-box {
    &__list {
        padding: 0;
        margin: 0;
        list-style: none;
        
        &__item {
            background-color: rgb(247, 247, 247);
            height: auto;
            padding: 20px;
            color: #839097;
            display: flex;
            margin-bottom: 5px;

            &__text {
                font-size: 14px;
                font-weight: lighter;
                margin-bottom: 0;
            }

        }
    }
}
