.banner-pagseguro {
    padding: 40px 0;
    min-height: 50px;
    background-color: #F0F0F7;
    margin-top: 60px;

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        width: 400px;
        height: 60px;
        object-fit: contain;

        @media screen and (max-width: $bp-MD){
            max-width: 300px;
        }
    }
}
