.finalizar-compra{
    
    .card-header{
        display: flex;
        justify-content: space-between;
    }

    .card{
        color: #839097;
        font-size: 15px;

        .text-bold{
            font-size: 16px;
            font-weight: bold;
            color: #81878b;
            letter-spacing: 0.5px;
        }

        label{
            text-transform: none;
        }
    }

    &__resumo{
        &__item{
            padding: 10px 0;
            border-bottom: 0.5px solid #F0F0F7;
            display: flex;

            img{
                border-radius: 10px;
                margin-right: 15px;
            }
        }

        &__subtotal{
            padding: 15px 0;
            display: flex;
            justify-content: flex-end;
            border-bottom: 0.5px solid #F0F0F7;

            .label{
                margin-right: 70px;
            }
        }
    }
}