.artigo-card{
    padding: 20px;
    height: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    text-align: center;
    @include card();

    &__img{
        width: 100%;
        height: 40%;

        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: 10px 10px 0 0;
        }
    }

    &__title{
        font-size: 19px;
        color: $mh-color-gray;
        max-height: 15%;
        overflow: hidden;
    }

    &__desc{
        font-size: 15px;
        font-weight: lighter;
        color: #989ea1;
        max-height: 19%;
        overflow: hidden;
    }
}