.headerlinks {
    padding: 20px 0;
    background-color: $mh-color-primary;
    @include transition();

    @media screen and(max-width: $bp-LG) {
        padding: 0;
        max-height: 0;

        &.active{
            padding: 20px 0;
            max-height: 1000px;
        }
    }

    &__list {
        display: flex;
        justify-content: space-around;

        @media screen and(max-width: $bp-LG){
            padding: 0px 20px;
            flex-wrap: wrap;
        }

        &__item {
            display: inline-block;
            @include transition();

            @media screen and(max-width: $bp-LG){
                margin: 5px 20px;
            }

            &:hover{
                @include scale(1.05);
            
            }

            a {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }

            &__label {
                color: #fff;
                font-size: 16px;
                font-weight: lighter;
            }

            &__icon {
                width: 40px;
                height: 50px;
                background-position: center center;
                background-size: contain;
                background-repeat: no-repeat;
                margin: 0;
                display: block;

                &.terapias {
                    background-image: url("/images/icones/terapias.png");
                }

                &.estetica{
                    background-image: url("/images/icones/estetica.png");
                }

                &.massagens{
                    background-image: url("/images/icones/massagens.png");
                }

                &.company{
                    background-image: url("/images/icones/company.png");
                }

                &.cursos{
                    background-image: url("/images/icones/cursos.png");
                }

                &.promocoes{
                    background-image: url("/images/icones/promocoes.png");
                }

                &.terapeutas{
                    background-image: url("/images/icones/terapeutas.png");
                }

                &.facilitadores{
                    background-image: url("/images/icones/facilitadores.png");
                }

                &.artigos{
                    background-image: url("/images/icones/artigos.png");
                }

                &.fotos{
                    background-image: url("/images/icones/fotos.png");
                }

                &.quemsomos{
                    background-image: url("/images/icones/quemsomos.png");
                }

                &.faleconosco{
                    background-image: url("/images/icones/faleconosco.png");
                }
            }
            @media screen and(max-width: $bp-MD) {
                display: block;
            }
        }

    }
}

.headerlinks-toggler{
    @media screen and(min-width: $bp-LG){
        display: none;
    }
}