.gallery {
    &__caption {
        display: flex;
        justify-content: center;
        align-items: center;

        &__title {
            @include title();
            max-width: 50%;
            text-align: center;
            color: $mh-color-primary;
            @media screen and(max-width: $bp-LG) {
                max-width: 80%;
            }
            @media screen and(max-width: $bp-MD) {
                max-width: 100%;
            }
        }

    }

    &__list {
        &__item {
            position: relative;
            width: 100%;
            height: 230px;
            margin-bottom: 20px;
            border-radius: 9px;
            overflow: hidden;

            &__media {
                width: 100%;
                height: 100%;
                object-fit: cover;
                filter: brightness(80%);
                cursor: pointer;
                @include transition();
                &:hover {
                    filter: brightness(100%);
                    @include scale(1.05);
                }
            }

            &__label {
                position: absolute;
                left: 0;
                bottom: 50px;
                color: #839097;
                @include card();
                padding: 5px 10px;
                font-size: 15px;
                border-radius: 0 5px 5px 0;
                z-index: 2;
            }

            &.image:after,
            &.video:after {
                content: '';
                position: absolute;
                top: 50%;
                right: 50%;
                font-family: FontAwesome;
                font-size: 40px;
                color: rgba(255, 255, 255, 0.116);
                text-align: center;
                transform: translate(50%, -50%);
                pointer-events: none;
            }
            &.image:after {
                content: '\f030';
            }
            &.video:after {
                content: '\f144';
            }
        }
    }
}
