.check-list {
    border-bottom: none;
    margin-bottom: 10px;

    &__header{
        padding: 10px 0;
        text-align: center;
        background-color:#cdd4d8;
        color: #607077;
        font-weight: bold;
    }
    
    &__list {
        border: 0.5px solid rgb(204, 204, 204);
        list-style: none;
        padding: 0;
        margin: 0;

        &__item {
            border-bottom: 0.5px solid rgb(204, 204, 204);
            padding: 15px 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &__checkbox {
                max-width: 60%;
                label {
                    font-size: 12px !important;
                    letter-spacing: 0.4px;
                    margin-bottom: 0;
                    padding: 0;
                }
            }

            &__price {
                color: $mh-color-primary;
                font-size: 23px;
                font-weight: lighter;
            }

            &.button {
                padding: 20px 15px;
                justify-content: center;
            }
        }
    }
}
