.page-header {
    height: 230px;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;

    &.sm{
        height: 180px;
    }

    &__caption {
        &__title {
            color: #fff;
            text-align: center;
            font-size: 60px;
            text-transform: uppercase;
            text-shadow: 2px 2px 4px rgba(8, 8, 8, 0.205);
            letter-spacing: 1.5px;

            @media screen and(max-width: $bp-MD){
                font-size: 40px;
            }
        }
    }
}
