$height: 480px;
$height-sm: 650px;

.home-slider {
    height: $height;
    margin-bottom: 90px;

    @media screen and(max-width: $bp-SM) {
        height: $height-sm;
        margin-bottom: 60px;
    }

    @media screen and (min-width: $bp-SM) and (max-width: $bp-LG)  {
        margin-bottom: 180px;
    }

    &__carousel {
        height: 100%;
        position: relative;

        .owl-nav {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;

            button {
                position: absolute;
                top: 40%;
                color: #fff !important;
                font-size: 40px !important;
                width: 90px;
                height: 70px;
                opacity: 0.8;
                text-align: center;
                @include transition();
                &:hover {
                    opacity: 1;
                }
                &:focus {
                    outline: none;
                }
                @media screen and(max-width: $bp-LG) {
                    top: 5%;
                }
            }

            .owl-prev {
                left: 5%;
            }

            .owl-next {
                right: 5%;
            }
        }

        &__item {
            position: relative;
            height: $height;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;

            @media screen and(max-width: $bp-LG) {
                height: $height-sm;
                background-position: right bottom;
                background-size: cover;
            }

            &__caption {
                position: absolute;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                color: #fff;
                text-transform: uppercase;
                text-align: center;
                
                @media screen and(max-width: $bp-LG) {
                    padding-top: 40px;
                    justify-content: flex-start;
                }

                &__title {
                    font-size: 72px;
                    letter-spacing: 10px;
                    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.089);
                    margin-bottom: 20px;
                    
                    @media screen and(max-width: $bp-SM){
                        margin-bottom: 0;
                    }
                    
                    @media screen and(max-width: $bp-LG) {
                        font-size: 35px;
                        letter-spacing: 5px;
                        max-width: 70%;
                        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.205);
                    }
                }

                &__subtitle {
                    font-size: 30px;
                    letter-spacing: 2px;
                    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.096);
                    font-weight: lighter;
                    
                    @media screen and(max-width: $bp-LG) {
                        max-width: 70%;
                        font-size: 18px;
                        letter-spacing: 1px;
                        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.219);
                    }
                }

                .animated {
                    -webkit-animation-duration: 1s;
                    animation-duration: 1s;
                    -webkit-animation-fill-mode: both;
                    animation-fill-mode: both;
                }

            }
        }
    }
}
