.trendings {

    &__caption {
        &__title {
            @include title();
            color: $mh-color-blue;

            @media screen and(max-width: $bp-LG) {
                font-size: 30px;
                text-align: left !important;
            }
        }

        @media screen and(max-width: $bp-LG) {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
        }
    }

    .owl-nav {
        display: flex;
        justify-content: flex-end;
        @media screen and(max-width: $bp-LG) {
            justify-content: center !important;
            margin-bottom: 10px;
        }

        button {
            cursor: pointer;
            background: none;
            outline: none;
            border: none;
            font-size: 30px;
            color: $mh-color-blue;
            &:not(:first-child) {
                margin-left: 6px;
            }
        }
    }

    &__row {
        padding: 30px 0;
    }

    .item {
        padding: 10px 15px;
    }
}
