.quem-somos{
    transform: translateY(-30px);

    &__card{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        padding: 50px 120px;
        text-align: center;

        @media screen and(max-width: $bp-MD){
            padding: 50px 20px;
            align-items: flex-start;

            .inner-desc, .inner-title{
                text-align: left;
            }
        }
    }

    label{
        text-align: left;
    }
}