.produto {
    width: 100%;
    height: 430px;
    margin-bottom: 20px;
    @include card();

    &__img{
        height: 38%;

        img{
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    &__desc {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 60%;
        padding: 20px 15px 15px;
        text-align: center;
        color: #839097;

        &__title{
            text-align: center;
            font-size: 20px;
            line-height: 21px;
            margin-bottom: 0;
        }
        
        &__rating{
            transform: scale(0.8);

            span{
                display: block;
                font-weight: lighter;
                font-size: 15px;
            }
        }

        &__price{

            &__info{
                display: block;
                font-size: 14px;
                font-weight: lighter;
            }
            
            &__value{
                color: $mh-color-primary;
                font-size: 30px;
                font-weight: bold;

                .sign{
                    font-size: 14px;
                    font-weight: 500;
                }
            }
        }

        &__more{
            justify-self: flex-end;
        }
    }
}
