.footer-contact {
    height: auto;
    padding: 40px 0;
    background-color: #DD4D71;
    color: #fff;
    font-size: 15px;
    font-weight: lighter;

    &__list {
        list-style: none;

        &__item {
            padding: 10px 0;
            color: #fff;
            font-size: 15px;

            .icon {
                margin-right: 7px;
                font-size: 16px;
            }
        }
    }

    &__medias {
        display: flex;
        flex-direction: column;
        height: 100%;

        @media screen and(max-width: $bp-SM){
            padding-top: 30px;
        }

        &__list {
            list-style: none;
            display: flex;
            justify-content: flex-end;

            &__item {
                display: inline-block;
                @include transition();

                &:not(:first-child) {
                    margin-left: 13px;
                }

                .icon {
                    font-size: 32px;
                    color: #fff;
                }

                &:hover{
                    @include scale(1.08);
                }
            }
        }

        &__mail{
            text-align: right;
            margin-top: auto;

            .icon{
                margin-right: 5px;
            }
        }
    }
}
