.artigo-inline {
    padding: 15px 0;
    display: flex;

    &__img {
        width: 30%;
        height: 150px;
        border-radius: 10px;
        overflow: hidden;
        margin-right: 10px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__caption {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;

        &__title {
            font-size: 19px;
            color: #839097;
        }

        &__desc {
            font-size: 15px;
            color: #919da3;
            font-weight: lighter;
            margin-bottom: 0;
        }
    }

    @media screen and(max-width: $bp-MD) {
        flex-direction: column;

        &__img {
            width: 100%;
            border-radius: 10px 10px 0 0;
            margin-bottom: 10px;
        }

        &__caption{
            width: 100%;
        }
    }
}
