.filter-box{
    padding: 20px 25px;
    width: 100%;
    background-color: #F0F0F7;
    border-radius: 13px;
    color: #839097;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.055);
    

    @media screen and(max-width: $bp-LG){
        margin-bottom: 30px;
    }

    &__header{
        display: flex;
        justify-content: space-between;
        text-transform: uppercase;
        font-size: 14px;

        &__label{
            color: #839097;
        }

        &__limpar{
            background-color: none;
            border: none;
            outline: none;
            color: $mh-color-primary;
            @include transition();

            &:hover, &:active{
                color: $mh-color-secondary;
            }
        }
    }

    &__filters{
        color: #839097;

        label{
            font-size: 14px;
            margin-bottom: 0;
        }

        &__item{
            padding: 3px 0;
            margin-bottom: 6px;

            label{
                min-width: 30%;
                margin-right: 20%;
            }
        }
    }
}