.terapeutaAvatar{
    padding: 10px 15px;

    
    &__img{
        width: 100px;
        height: 130px;
        border-radius: 50%;
        object-fit: cover;
        margin-bottom: 10px;

        @media screen and(max-width: $bp-SM){
            height: 115px;
        }
    }

    &__nome{
        text-align: center;
        color: rgb(143, 143, 143);
    }
}