.search-terapeuta {
    position: relative;
    z-index: 5;
    transform: translateY(-130px);
    
    @media screen and(max-width: $bp-LG) {
        transform: translateY(-330px);
    }

    @media screen and(max-width: $bp-SM) {
        transform: translateY(-470px);
    }

    &__title {
        font-size: 22px;
        text-align: center;
        color: $mh-color-secondary;
        margin-bottom: 30px;
        @media screen and(max-width: $bp-SM) {
            font-size: 18px;
        }
    }

    &__content {
        padding: 30px 40px;
        @include card();
    }

    .col--send {

        button {
            margin-top: 30px;
            @media screen and (max-width: $bp-LG) {
                margin-top: 0;
            }
        }

    }

}
