.artigo{
    .container{
        padding: 0 200px;

        @media screen and(max-width: $bp-LG){
            padding: 0 100px;
        }

        @media screen and(max-width: $bp-MD){
            padding: 0 30px;
        }
    }

    &__header{
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;

        &__title{
            font-weight: lighter;
            font-size: 40px;
            color: $mh-color-primary;
        }

        &__info{
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            color: #839097;

            @media screen and(max-width: $bp-MD){
                flex-direction: column;
            }

            &__date{
                display: block;
                font-weight: lighter;
            }

            &__author{
                font-weight: bold;
            }

            &__share{
                padding-top: 10px;
            }
        }
    }

    &__content{
        color: #949ea3;
        font-size: 16px;

        img{
            border-radius: 10px;
            width: 100%;
            margin: 25px 0;
        }
        
    }
}