.testimonial {
    display: flex;
    flex-direction: column;
    padding: 20px 0;
    margin-bottom: 10px;

    &:not(:last-child) {
        border-bottom: 1px solid rgb(212, 212, 212);
    }

    &__header {
        display: flex;
        height: 100px;
        justify-content: space-between;
        align-items: center;

        &__author {
            height: 100%;
            display: flex;
            align-items: center;

            &__img {
                height: 100%;
                width: 100px;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 15px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    @include transition();
                    &:hover {
                        @include scale(1.05);
                    }
                }
            }

            &__info {
                display: flex;
                flex-direction: column;

                &__name {
                    font-weight: bold;
                    color: $mh-color-gray;
                    font-size: 18px;
                }

                &__title {
                    font-weight: lighter;
                    color: $mh-color-gray;
                    font-size: 14px;
                }
            }
        }
    }

    &__desc {
        padding-top: 15px;
        font-weight: lighter;
        color: rgb(150, 150, 150);

        p {
            margin-bottom: 0;
        }
    }
}
