.terapeuta{
    transform: translateY(-30px);

    &__card{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        padding: 20px 120px;
        padding-top: 100px;

        @media screen and(max-width: $bp-MD){
            padding: 20px 40px;
            padding-top: 100px;
        }
    }

    &__img{
        width: 150px;
        height: 150px;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        top: -70px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__name{
        font-size: 40px;
        color: $mh-color-primary;
        font-weight: lighter;
    }

    &__bio{
        padding: 30px 0;
        color: rgb(175, 175, 175);
        font-size: 15px;
        font-size: 15px;
        text-align: center;
        font-weight: lighter;
        border-bottom: 1px solid rgb(146, 146, 146);
    }

    &__skills{
        display: flex;
        align-items: center;

        @media screen and(max-width: $bp-MD){
            flex-direction: column;
        }
    }

    .caption{
        font-weight: lighter;
        font-size: 14px;
        color: rgb(175, 175, 175);
    }
}