.cursoHero{
    padding: 40px 0;

    &__img{
        width: 100%;
        height: 400px;
        object-fit: cover;
        border-radius: 14px;
    }

    &__caption{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100%;
        text-align: left;

        &__title{
            @include title();
            color: $mh-color-primary;

            @media screen and(max-width: $bp-MD){
                font-size: 40px;
            }
        }

        &__desc{
            font-size: 17px;
            font-weight: 500;
            color: #839097;
        }
    }
}